.container {
    margin: 0 auto;
    @media (min-width: 320px) {
      padding: 0 15px;
    }
    @media (min-width: 768px) {
      padding: 0;
      max-width: 738px;
    }
    @media (min-width: 1200px) {
      max-width: 1170px;
    }
  }
  * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  a {
    text-decoration: none;
  }
  li {
    list-style-type: none;
  }